import cx from 'clsx';
import React, { FC, MouseEvent, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Button, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, CustomizacaoTermo } from '../../../../models/dtos/mcg/customizacaoTermo';
import {
  ABERTURA_ENCERRAMENTO,
  BALANCO_PATRIMONIAL,
  CARTA_RESPONSABILIDADE,
  CUSTOMIZACAO_TERMO_ENUM_VALUES,
  DEMONSTRCAO_RESULTADO,
  getTermoDefaultAbertura,
  getTermoDefaultEncerramento,
  VERACIDADE,
} from '../../../../models/enumerated/mcg/customizacaoTermoEnum';
import { CUSTOMIZACAO_TERMO_TAGS_ABERTURA_ENCERRAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoTagsAberturaEncerramentoEnum';
import { CUSTOMIZACAO_TERMO_TAGS_BALANCO_PATRIMONIAL_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoTagsBalancoPatrimonialEnum';
import { CUSTOMIZACAO_TERMO_TAGS_DEMONSTRACAO_RESULTADO_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoTagsDemonstracaoResultadoEnum';
import { CUSTOMIZACAO_TERMO_TAGS_RESPONSABILIDADE_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoTagsResponsabilidadeEnum';
import { CUSTOMIZACAO_TERMO_TAGS_VERACIDADE_ENUM_VALUES } from '../../../../models/enumerated/mcg/customizacaoTermoTagsVeracidadeEnum';

interface GrupoHistoricoEditViewProps extends EditViewProps<CustomizacaoTermo> {}

const CustomizacaoTermoEditView: FC<GrupoHistoricoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CustomizacaoTermo>({ defaultValues: selectedItem, mode: 'onBlur' });
  const [activeTab, setActiveTab] = useState(1);

  const [isTag, setIsTag] = useState(null);
  const [isDisableTermoEncerramento, setIsDisableTermoEncerramento] = useState(false);

  const [isTagTermos, setIsTagTermos] = useState(CUSTOMIZACAO_TERMO_TAGS_RESPONSABILIDADE_ENUM_VALUES);

  const [isTitleAberturaTag, setIsTitleAberturaTag] = useState('Termo Abertura');
  const [isTitleEncerramentoTag, setIsTitleEncerramentoTag] = useState('Termo Encerramento');

  const termoAberturaRef = useRef<HTMLTextAreaElement>();
  const termoEncerramentoRef = useRef<HTMLTextAreaElement>();

  const restaurarTermoPadrao = () => {
    if (activeTab === 1 && formProps.getValues().tipo) {
      formProps.setValue('termoAbertura', getTermoDefaultAbertura(formProps.getValues().tipo));
    } else if (activeTab === 2 && formProps.getValues().tipo) {
      formProps.setValue('termoEncerramento', getTermoDefaultEncerramento(formProps.getValues().tipo));
    }
  };

  const funcInsertTaginState = (valor: any) => {
    setIsTag(valor);
  };

  const funcInsertTagTermoWithComboBox = () => {
    const termoA = formProps.getValues().termoAbertura;
    const termoE = formProps.getValues().termoEncerramento;
    if (activeTab === 1 && termoAberturaRef.current && isTag !== null) {
      const { selectionStart } = termoAberturaRef.current;
      const resultStr = termoA.substring(0, selectionStart) + isTag + termoA.substring(selectionStart, termoA.length);
      formProps.setValue('termoAbertura', resultStr);
    } else if (activeTab === 2 && termoEncerramentoRef.current && isTag !== null) {
      const { selectionStart } = termoEncerramentoRef.current;
      const resultStr = termoE.substring(0, selectionStart) + isTag + termoE.substring(selectionStart, termoE.length);
      formProps.setValue('termoEncerramento', resultStr);
    }
  };

  const cleanTextArea = () => {
    formProps.setValue('termoAbertura', '');
    formProps.setValue('termoEncerramento', '');
  };

  const ajustarTermos = termo => {
    cleanTextArea();
    ajustarTabs(termo);
  };

  const ajustarTabs = (termo = null) => {
    switch (termo) {
      case ABERTURA_ENCERRAMENTO.value:
        setIsTitleAberturaTag('Termo de Abertura');
        setIsTitleEncerramentoTag('Termo de Encerramento');
        setIsTagTermos(CUSTOMIZACAO_TERMO_TAGS_ABERTURA_ENCERRAMENTO_ENUM_VALUES);
        setIsDisableTermoEncerramento(false);
        break;
      case VERACIDADE.value:
        setIsTitleAberturaTag('Contabilista');
        setIsTitleEncerramentoTag('Responsável Legal');
        setIsTagTermos(CUSTOMIZACAO_TERMO_TAGS_VERACIDADE_ENUM_VALUES);
        setIsDisableTermoEncerramento(false);
        break;
      case BALANCO_PATRIMONIAL.value:
        setIsTitleAberturaTag('Termo do Balanço Patrimonial');
        setIsTitleEncerramentoTag('');
        setIsDisableTermoEncerramento(true);
        setIsTagTermos(CUSTOMIZACAO_TERMO_TAGS_BALANCO_PATRIMONIAL_ENUM_VALUES);
        break;
      case DEMONSTRCAO_RESULTADO.value:
        setIsTitleAberturaTag('Termo de Demonstração do Resultado');
        setIsTitleEncerramentoTag('');
        setIsDisableTermoEncerramento(true);
        setIsTagTermos(CUSTOMIZACAO_TERMO_TAGS_DEMONSTRACAO_RESULTADO_ENUM_VALUES);
        break;
      case CARTA_RESPONSABILIDADE.value:
        setIsTitleAberturaTag('Carta de Responsabilidade');
        setIsTitleEncerramentoTag('');
        setIsDisableTermoEncerramento(true);
        setIsTagTermos(CUSTOMIZACAO_TERMO_TAGS_RESPONSABILIDADE_ENUM_VALUES);
        break;
    }
  };

  useEffect(
    () => {
      formProps.setValue('modelo', 'F');
      ajustarTabs();
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  return (
    <WindowComponent title="Manutenção de Termos de Abertura e Encerramento">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="modelo" hidden={true} isPrependLabel={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição do Grupo."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipo"
              label="Tipo"
              isPrependLabel={true}
              onChange={ajustarTermos}
              placeholder="Selecione um Tipo"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: CUSTOMIZACAO_TERMO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Nav tabs={true}>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
                  {isTitleAberturaTag}
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
                  {isTitleEncerramentoTag}
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent className="mb-3" activeTab={activeTab}>
              <TabPane tabId={1} className="mb-0 p-3">
                <InputGroupInline
                  formProps={formProps}
                  id="termoAbertura"
                  inputProps={{
                    ref: e => {
                      formProps.register(e);
                      termoAberturaRef.current = e;
                    },
                    rows: 15,
                  }}
                  isPrependLabel={true}
                  hideLabel={true}
                  type={InputType.TEXT_AREA}
                />
              </TabPane>
              <TabPane tabId={2} className="mb-0 p-3" disabled={isDisableTermoEncerramento}>
                <InputGroupInline
                  formProps={formProps}
                  id="termoEncerramento"
                  inputProps={{
                    ref: e => {
                      formProps.register(e);
                      termoEncerramentoRef.current = e;
                    },
                    rows: 15,
                  }}
                  isPrependLabel={true}
                  hideLabel={true}
                  type={InputType.TEXT_AREA}
                />
              </TabPane>
            </TabContent>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col xs={8}>
            <InputGroupInline
              id="tags"
              label="Tags"
              isPrependLabel={true}
              placeholder="Selecione uma tag"
              onChange={funcInsertTaginState}
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: isTagTermos,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <Button id={'addTermo'} color="primary" className="mr-2" onClick={funcInsertTagTermoWithComboBox}>
              Incluir
            </Button>
            <Button id={'restPadrao'} color="warning" onClick={restaurarTermoPadrao}>
              Restaurar Padrão
            </Button>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CustomizacaoTermoEditView;
