import { CARTA_RESPONSABILIDADE } from '../../enumerated/mcg/customizacaoTermoEnum';
import { CustomizacaoTermosDefaultContent } from '../mfp/customizacaoTermosDefaultContent';

export class CustomizacaoTermosCartaResponsabilidadeDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Prezados Senhores:\n' +
    'Declaramos, para os devidos fins, como administrador e responsável legal da empresa <R_SOC_EMP/>, CNPJ <CNPJ_CPF_EMP/>, que as informações relativas ao período base <PER_APURACAO/>, fornecidos a Vossas Senhorias para escrituração e elaboração das demonstrações contábeis, obrigações acessórias, apuração de impostos e arquivos eletrônicos exigidos pela fiscalização federal, estadual, municipal, trabalhista e previdenciária são fidedignas.\n' +
    'Também declaramos:\n' +
    '  (a). Que os controles internos adotados pela nossa empresa são de responsabilidade da administração e estão adequados ao tipo de atividade e volume de transações;\n' +
    '  (b). Que não realizamos nenhum tipo de operação que possa ser considerada ilegal, frente à legislação vigente;\n' +
    '  (c). Que todos os documentos que geramos e recebemos de nossos fornecedores estão revestidos de total idoneidade;\n' +
    '  (d). Que os estoques registrados em conta própria foram por nós avaliados, contados e levantados fisicamente e perfazem a realidade do período encerrado em <DT_PER_APURACAO_FIM/>;\n' +
    '  (e). Que as informações registradas no sistema de gestão e controle interno, denominado Calima ERP Contábil, são controladas e validadas com documentação suporte adequada, sendo de nossa inteira responsabilidade todo o conteúdo do banco de dados e arquivos eletrônicos gerados.\n' +
    'Além disso, declaramos que não temos conhecimento de quaisquer fatos ocorridos no período base que posssam afetar as demonstrações contábeis ou que as afetam até a data desta carta ou, ainda, que possam afetar a continuidade das operações da empresa.\n' +
    'Também confirmamos que não houve:\n' +
    '  (a). Fraude envolvendo administração ou empregados em cargos de responsabilidade ou confiança;\n' +
    '  (b). Fraude envolvendo terceiros que poderiam ter efeito material nas demonstrações contábeis;\n' +
    '  (c). Violação ou possíveis violações de lei, normas ou regulamentos cujos efeitos deveriam ser considerados para divulgação nas demonstrações contábeis, ou mesmo dar origem ao registro de provisão para contingências passivas.';

  public getCustomizacaoTermosTipoEnum() {
    return CARTA_RESPONSABILIDADE;
  }

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }
}
