import { CustomizacaoTermosAberturaEncerramentoCabecalhoDefault } from '../../dtos/mcg/customizacaoTermosAberturaEncerramentoCabecalhoDefault';
import { CustomizacaoTermosAberturaEncerramentoRodapeDefault } from '../../dtos/mcg/customizacaoTermosAberturaEncerramentoRodapeDefault';
import { CustomizacaoTermosBalancoPatrimonialDefault } from '../../dtos/mcg/customizacaoTermosBalancoPatrimonialDefault';
import { CustomizacaoTermosCartaResponsabilidadeDefault } from '../../dtos/mcg/customizacaoTermosCartaResponsabilidadeDefault';
import { CustomizacaoTermosDemonstracaoResultadoDefault } from '../../dtos/mcg/customizacaoTermosDemonstracaoResultadoDefault';
import { CustomizacaoTermosVeracidadeCabecalhoDefault } from '../../dtos/mcg/customizacaoTermosVeracidadeCabecalhoDefault';
import { CustomizacaoTermosVeracidadeRodapeDefault } from '../../dtos/mcg/customizacaoTermosVeracidadeRodapeDefault';

export const ABERTURA_ENCERRAMENTO = { value: 'ABERTURA_ENCERRAMENTO', label: 'Termo de Abertura e Encerramento' };
export const VERACIDADE = { value: 'VERACIDADE', label: 'Declaração Individualizada de Veracidade' };
export const BALANCO_PATRIMONIAL = { value: 'BALANCO_PATRIMONIAL', label: 'Termo do Balanço Patrimonial' };
export const DEMONSTRCAO_RESULTADO = { value: 'DEMONSTRCAO_RESULTADO', label: 'Termo da Demonstração do Resultado' };
export const CARTA_RESPONSABILIDADE = { value: 'CARTA_RESPONSABILIDADE', label: 'Carta de Responsabilidade' };

export function getTermoDefaultAbertura(customizacaoTipoEnum: string) {
  const result = getAllDefaultContentsAbertura().find(it => it.getCustomizacaoTermosTipoEnum().value === customizacaoTipoEnum);
  return result === null ? null : result.getTermoDefault();
}

export function getTermoDefaultEncerramento(customizacaoTipoEnum: string) {
  const result = getAllDefaultContentsEncerrramento().find(it => it.getCustomizacaoTermosTipoEnum().value === customizacaoTipoEnum);
  return result === null ? null : result.getTermoDefault();
}

export function getAllDefaultContentsAbertura() {
  return [
    new CustomizacaoTermosAberturaEncerramentoCabecalhoDefault(),
    new CustomizacaoTermosBalancoPatrimonialDefault(),
    new CustomizacaoTermosCartaResponsabilidadeDefault(),
    new CustomizacaoTermosDemonstracaoResultadoDefault(),
    new CustomizacaoTermosVeracidadeCabecalhoDefault(),
  ];
}

export function getAllDefaultContentsEncerrramento() {
  return [new CustomizacaoTermosAberturaEncerramentoRodapeDefault(), new CustomizacaoTermosVeracidadeRodapeDefault()];
}

export const CUSTOMIZACAO_TERMO_ENUM_VALUES = [ABERTURA_ENCERRAMENTO, VERACIDADE, BALANCO_PATRIMONIAL, DEMONSTRCAO_RESULTADO, CARTA_RESPONSABILIDADE];
